.day-rutine{
    color: white;
    display: flex;
    flex-direction: column;
}

.exercise{
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 1.5rem 0;
    border-bottom: 1px solid lightgray;
    font-size: 16px;
    font-family: Arboria;
}

.exercise-details{
    display: flex;
    flex-direction: column;
}

.day{
    margin-bottom: 1rem;
    font-size: 25px;
}

.exercise-image{
    width: 30%;
    border-radius: 10px;
}